<template>
  <v-main>
	<v-container fluid>
		<v-row dense>
			<v-col
				v-for="card in projects"
				:key="card.id_aplikasi"
				:cols="4">
				<v-card
					class="mx-auto"
					max-width="344"
					outlined
				>
				<v-list-item three-line>
					<v-list-item-content>
						<v-card-subtitle v-text="card.nama_aplikasi"></v-card-subtitle>
					<v-list-item-subtitle v-text="card.description"></v-list-item-subtitle>
					</v-list-item-content>
					<v-avatar
						class="ma-3"
						size="125"
					>
					<v-img :src="card.url_image"
						class="white--text align-end"
						gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)"
						height="120px" width="40"/>
					</v-avatar>
				</v-list-item>
				<v-card-actions>
					<v-btn
						outlined
						rounded
						text
						:href="card.url_download"
					>
					Download
					</v-btn>
				</v-card-actions>
				</v-card>
			</v-col>
		</v-row>
	</v-container>
	<v-container fluid>
		<v-col>
			<v-card-text>
				<v-spacer></v-spacer>
			</v-card-text>
		</v-col>
	</v-container> 
  </v-main>
</template>

<script>
  import axios from "axios";
  export default {
    data: () => ({
		loading: false,
		drawer: false,
		master: [],
		projects: [],
		show: false,
    }),
	mounted(){
			this.getItems();
			this.getProjects();
		},
    methods: {
			getItems: function () {
				this.loading = true;
				axios.get('https://core.ladokutu.info/index.php/Web/menu_web')
					.then(res => {
					this.master=res.data;
				})
				.catch(err => {
					console.log(err);
				})
				this.loading = false;
			},
			getProjects: function () {
				this.loading = true;
				axios.get('https://core.ladokutu.info/index.php/Web/content_projects')
					.then(res => {
					this.projects=res.data;
				})
				.catch(err => {
					console.log(err);
				})
				this.loading = false;
			},
    },
  }
</script>